:root {
  --color-brand--1: #ffb545;
  --color-brand--2: #00c46a;
  --color-dark--1: #2d3439;
  --color-dark--2: #42484d;
  --color-light--1: #aaa;
  --color-light--2: #ececec;
  --color-light--3: #d6dee0;
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: var(--color-light--2);
  overscroll-behavior-y: none;
  background: linear-gradient(to bottom right, #42484d, #00c46a, #2d3439);
  height: 100vh;
  padding: 2.5em;
  font-family: Manrope, sans-serif;
  font-weight: 400;
  line-height: 1.6;
  display: flex;
}

.modal {
  color: #e8e8e9;
  z-index: 1000;
  background-color: #3c3f41;
  max-width: 70rem;
  padding: 5rem 6rem;
  font-size: 1.75rem;
  transition: all .5s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3.5rem 6rem #0009;
}

.modal h3 {
  text-align: center;
}

.btn--close-modal {
  color: #00c46a;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: 4rem;
  display: flex;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.btn--close-modal:hover {
  color: #3c3f41;
  background-color: #00c46a;
  border-radius: .1rem;
}

.hidden {
  display: none;
}

a:link, a:visited {
  color: var(--color-brand--1);
}

.sidebar {
  background-color: var(--color-dark--1);
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  flex-direction: column;
  flex-basis: 50rem;
  padding: 3rem 5rem 4rem;
  display: flex;
}

#map {
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
  box-shadow: 10rem 1.5rem 10rem #0006;
}

.sidebar {
  box-shadow: 0 1.5rem 10rem #0e0e0ecc;
}

.logo {
  align-self: center;
  height: 5.2rem;
  margin-bottom: 4rem;
}

.handle-workout {
  display: flex;
}

.handle-workout--sort {
  margin-right: 5em;
}

.workouts {
  height: 77vh;
  list-style: none;
  overflow-y: scroll;
}

.workouts::-webkit-scrollbar {
  width: .5em;
  height: .5em;
}

.workouts::-webkit-scrollbar-track {
  scroll-behavior: smooth;
  border-radius: 10vw;
}

.workouts::-webkit-scrollbar-thumb {
  background: #00c46a;
  border: 1.25em solid #00c46a;
  border-radius: 10vw;
}

.workout {
  background-color: var(--color-dark--2);
  cursor: pointer;
  border-radius: 5px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: .75rem 1.5rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem 2.25rem;
  display: grid;
}

.workout--running {
  border-left: 5px solid var(--color-brand--2);
}

.workout--cycling {
  border-left: 5px solid var(--color-brand--1);
}

.workout__title {
  grid-column: 1 / -1;
  font-size: 1.7rem;
  font-weight: 600;
}

.workout__details {
  align-items: baseline;
  display: flex;
}

.workout__icon {
  height: .28rem;
  margin-right: .2rem;
  font-size: 1.8rem;
}

.workout__value {
  margin-right: .5rem;
  font-size: 1.5rem;
}

.workout__unit {
  color: var(--color-light--1);
  text-transform: uppercase;
  font-size: 1.1rem;
  font-weight: 800;
}

.form {
  background-color: var(--color-dark--2);
  border-radius: 5px;
  grid-template-columns: 1fr 1fr;
  gap: .5rem 2.5rem;
  height: 9.25rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem 2.75rem;
  transition: all .5s, transform 1ms;
  display: grid;
}

.form.hidden {
  opacity: 0;
  height: 0;
  margin-bottom: 0;
  padding: 0 2.25rem;
  transform: translateY(-30rem);
}

.form__row {
  align-items: center;
  display: flex;
}

.form__row--hidden, .hidden {
  display: none;
}

.form__label {
  flex: 0 0 50%;
  font-size: 1.5rem;
  font-weight: 600;
}

.form__input {
  color: #e8e8e9;
  background-color: #9c9c9c;
  border: none;
  border-radius: 3px;
  width: 100%;
  padding: .3rem 1.1rem;
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 900;
  transition: all .2s;
}

.handle-workout .form__input {
  background-color: var(--color-dark--2);
  box-shadow: .25rem .75rem 3rem #0a0a0a66;
}

.handle-workout .form__input--value {
  background-color: var(--color-dark--2);
}

.form__input:focus {
  outline: none;
}

.delete-all {
  cursor: pointer;
  background-color: #0000;
  background-repeat: no-repeat;
  border: none;
  outline: none;
  font-size: 20px;
  overflow: hidden;
}

.fa-trash-can {
  color: #8b8b8b;
}

.fa-trash-can:hover {
  color: #adadad;
}

.form__input--value {
  background-color: #8b8b8b;
  font-weight: 900;
}

.form__btn {
  display: none;
}

.copyright {
  text-align: center;
  color: var(--color-light--1);
  margin-top: auto;
  font-size: 1.3rem;
}

.twitter-link:link, .twitter-link:visited {
  color: var(--color-light--1);
  transition: all .2s;
}

.twitter-link:hover, .twitter-link:active {
  color: var(--color-light--2);
}

#map {
  background-color: var(--color-light--1);
  flex: 1;
  height: 100%;
}

.leaflet-popup .leaflet-popup-content-wrapper {
  background-color: var(--color-dark--1);
  color: var(--color-light--2);
  border-radius: 5px;
  padding-right: .6rem;
}

.leaflet-popup .leaflet-popup-content {
  font-size: 1.5rem;
}

.leaflet-popup .leaflet-popup-tip {
  background-color: var(--color-dark--1);
}

.running-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--2);
}

.cycling-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--1);
}

